    background-repeat: no-repeat;
    background-size: cover;
    width: clamp(331px, 36.467vw, 547px);
    height: clamp(331px, 36.467vw, 547px);
    position: absolute;
    content: "";
    z-index: -1;
    left: -1%;
    top: 50%;
    transform: translate(-40%, -50%);

    @include laptop {
      left: 50%;
      top: initial;
      bottom: 0;
      transform: translate(-50%, 25%);
    }

    @include mobile {
      transform: translate(-50%, 21%);
    }
  }

  @include small-tablet {
    max-width: initial;
    justify-self: start;
    width: calc(100% + (var(--container-offset) * 2));
    margin-left: calc((var(--container-offset) * -1));
    padding: clamp(34px, 2.667vw, 40px) var(--container-offset);
  }

  &--mode {
    padding: clamp(33px, 2.667vw, 40px) clamp(10px, 4vw, 60px)
      clamp(27px, 2.467vw, 37px);
    height: fit-content;

