      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      content: "";
      height: clamp(58px, 14.583vw, 112px);
      width: clamp(243px, 60.547vw, 465px);
      margin-top: 5px;

      @include mobile {
        margin-top: 0;
      }
    }

    @include mobile {
      grid-gap: 25px;

      .main-form__title {
        margin-bottom: 5px;
      }

      .main-form__button {
        margin-top: 5px;
      }
    }
  }
  &__label {
    width: 100%;
  }

  &__input {
    height: 44px;
    text-indent: clamp(16px, 1.467vw, 22px);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 1.067vw, 16px);
    line-height: 130%;
    color: rgba(var(--cl-c), 1);

    &::placeholder {
      color: rgba(var(--cl-f), 1);
    }
  }

  &__title {
    margin-bottom: clamp(10px, 1.333vw, 20px);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: clamp(16px, 1.6vw, 24px);
    line-height: 150%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: rgba(var(--cl-c), 1);
  }

  &__link {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: rgba(var(--cl-d), 1);
    padding-top: clamp(4px, 0.667vw, 10px);

    @include mobile {
      padding-top: 0;
    }
  }

  &__deskr {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(12px, 0.933vw, 14px);
    line-height: 120%;
    color: rgba(var(--cl-f), 1);
    padding-top: 4px;

    @include mobile {
      padding: 0;
      margin-top: -15px;
    }
  }

  &__button {
    margin-top: clamp(10px, 1.333vw, 20px);
    width: clamp(300px, 21.333vw, 320px);
    height: 59px;

    @include ios {
      width: 100%;
    }
  }
}

.ordering-form {
  max-width: 978px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px 32px;

  @include small-tablet {
    grid-template-columns: 1fr;
  }

  &::before {
    display: none;
  }
  label[for="user"] {
    order: 1;
  }
  label[for="number"] {
    order: 3;

    @include small-tablet {
      order: 2;
    }
  }
  label[for="mail"] {
    order: 5;
    @include small-tablet {
      order: 3;
    }
  }
  label[for="adress"] {
    order: 7;
    @include small-tablet {
      order: 6;
    }
  }
  label[for="street"] {
    order: 8;
    @include small-tablet {
      order: 7;
    }
  }
  label[for="company"] {
    order: 9;
    @include small-tablet {
      order: 12;
    }
  }
  label[for="house"] {
    order: 10;
    @include small-tablet {
      order: 8;
    }
  }
  label[for="code"] {
    order: 11;
    @include small-tablet {
      order: 13;
    }
  }
  label[for="apartment"] {
    order: 12;
    @include small-tablet {
      order: 9;
    }
  }
  label[for="register"] {
    order: 13;
    @include small-tablet {
      order: 14;
    }
  }
  .main-form__area {
    order: 14;
    grid-column: 1;
    @include small-tablet {
      order: 15;
    }
  }

  .select.delivery {
    order: 2;
    @include small-tablet {
      order: 4;
    }
  }
  .select.pay {
    order: 4;
    @include small-tablet {
      order: 10;
    }
  }
  .select.city {
    order: 5;
  }
  p {
    grid-row: 5 / 7;
    order: 6;
    @include small-tablet {
      order: 11;
      grid-row: initial;
    }
  }

  p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: rgba(var(--cl-g), 1);
    b {
      color: rgba(var(--cl-d), 1);
    }
  }
  .main-form {
    &__title {
      grid-column: span 2;

      @include small-tablet {
        grid-column: initial;
      }
    }

    &__input {
      text-indent: 16px;
    }

    &__button {
      width: clamp(300px, 27.133vw, 407px);
      justify-self: center;
      grid-column: span 2;
      order: 15;

      @include small-tablet {
        grid-column: initial;
        order: 16;
      }
    }

    &__area {
      padding-top: clamp(8px, 0.8vw, 12px);
      min-height: clamp(120px, 12vw, 180px);
      text-indent: clamp(16px, 1.467vw, 22px);
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 400;
      font-size: clamp(14px, 1.067vw, 16px);
      line-height: 130%;
      color: rgba(var(--cl-c), 1);

      &::placeholder {
        color: rgba(var(--cl-f), 1);
      }
    }
  }
}
