.news-about {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: clamp(10px, 2.533vw, 38px);

  @include tablet {
    grid-template-columns: 1fr 2.5fr;
  }

  @include small-tablet {
    grid-template-columns: 100%;
  }

  @include mobile {
    grid-gap: 53px;
  }

  &__aside {
    display: flex;
    flex-direction: column;
    grid-gap: clamp(20px, 2vw, 30px);
  }

  &__inner {
    display: flex;
    flex-direction: column;
    grid-gap: clamp(15px, 1.333vw, 20px);

    &--mode {
      grid-gap: clamp(15px, 1.067vw, 16px);
    }
  }

  &__title {
    padding-bottom: 7px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(24px, 2.267vw, 34px);
    line-height: 150%;
    color: rgba(var(--cl-c), 1);

    @include mobile {
      padding-bottom: 10px;
    }

    &--mode {
      padding-bottom: 0;
      font-size: clamp(20px, 1.6vw, 24px);
      line-height: 150%;
    }
  }

  &__subtitle {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: clamp(16px, 1.2vw, 18px);
    line-height: 150%;
    text-transform: uppercase;
    color: rgba(var(--cl-c), 1);
  }

  &__box {
    display: flex;
    flex-direction: column;
    grid-gap: clamp(20px, 2vw, 30px);

    P {
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: rgba(var(--cl-c), 1);
    }
  }

  &__faq {
    display: flex;
    flex-direction: column;
    grid-gap: 30px;

    .main-pretitle {
      font-weight: 500;
      font-size: clamp(20px, 1.6vw, 23px);
      line-height: 130%;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    grid-gap: 18px;

    @include mobile {
      grid-gap: 20px;
    }
  }

  &__date {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 162%;
    color: rgba(var(--cl-f), 1);
  }

  &__buttons {
    padding-top: 5px;
    display: flex;
    align-items: center;
    grid-gap: 14px;

    @include mobile {
      padding-top: 0;
    }
  }

  &__wrapp {
    display: flex;
    flex-direction: column;
    grid-gap: 14px;
    margin-top: -2px;

    @include mobile {
      grid-gap: 16px;
    }
  }

  &__button {
    padding: 12px 30px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: rgba(var(--cl-g), 1);
    background-color: rgba(var(--bg-l), 1);
  }
}

.main-comments {
  padding-top: clamp(10px, 2vw, 30px);
}

.reviews-form {
  padding-top: clamp(10px, 1.333vw, 20px);
  max-width: 100%;
  justify-content: center;

  &__label {
    max-width: 600px;
  }
}

.blog-about {
  grid-template-columns: auto 1fr 0.7fr;

  @include desktop {
    grid-template-columns: auto 1fr 0.3fr;
  }

  @include laptop {
    grid-template-columns: auto 1fr auto;
  }

  @include tablet {
    grid-template-columns: 0.4fr 1fr;
  }

  @include small-tablet {
    grid-template-columns: 1fr;
  }

  .news-about__title {
    @include mobile {
      padding: 0;
    }
  }

  .news-about__box {
    grid-gap: 30px;
  }
  .user-card {
    @include tablet {
      display: none;
    }
  }
}
