.discount-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 12px;

  @include tablet {
    grid-template-columns: repeat(2, 1fr);
  }

  @include mobile {
    grid-template-columns: 1fr;
  }

  &__item {
    @include mobile {
      &:nth-child(n + 12) {
        display: none;
      }
    }
  }
}
