.product-nav {
  margin-bottom: clamp(30px, 3.533vw, 53px);

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 12px;

    @include tablet {
      grid-gap: 10px;
    }

    @include small-tablet {
      grid-gap: 15px;
      flex-wrap: nowrap;
      overflow-x: scroll;
    }

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__btn {
    padding: 0 15px;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 7px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: clamp(16px, 1.2vw, 18px);
    line-height: 120%;
    color: rgba(var(--cl-g), 1);
    background: rgba(var(--bg-l), 1);
    transition: all 0.4s;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      right: 0;
      left: 0;
      bottom: 0;
      border-bottom: 2px solid transparent;
      transition: all 0.4s;
    }

    @include tablet {
      padding: 0 11px;
    }

    @include small-tablet {
      width: 205px;
    }

    b {
      color: rgba(var(--cl-c), 1);
    }

    &.active {
      &:before {
        border-bottom: 2px solid var(--border-c);
      }
    }
  }
}
