.blog-tabs {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(30px, 2.6vw, 39px);

  &__nav {
    display: flex;
    grid-gap: 10px;
    flex-wrap: wrap;
  }

  &__btn {
    padding: 12px 30px;
    background: rgba(var(--bg-l), 1);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: rgba(var(--cl-g), 1);
    position: relative;
    transition: all 0.4s;

    &.active {
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        bottom: 0;
        background-color: rgba(var(--bg-c), 1);
        transition: all 0.4s;
      }
    }
  }
}
