.reviews-form {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  max-width: 600px;
  width: 100%;
  align-items: center;

  &__label {
    width: 100%;
  }

  &__title {
    padding-bottom: 20px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: clamp(20px, 1.6vw, 24px);
    line-height: 125%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: rgba(var(--cl-b), 1);
  }

  &__input {
    border: 1px solid var(--border-b);
    padding: 12px 16px;
    color: rgba(var(--cl-b), 1);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 1.067vw, 16px);
    line-height: 120%;

    &::placeholder {
      color: rgba(var(--cl-f), 1);
    }

    @include mobile {
      padding: 14px 20px;
    }
  }

  &__area {
    margin-bottom: clamp(10px, 2vw, 30px);
    border: 1px solid var(--border-b);
    width: 100%;
    height: clamp(180px, 15.333vw, 230px);
    padding: 11px 16px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 1.067vw, 16px);
    line-height: 120%;
    color: rgba(var(--cl-b), 1);

    &::placeholder {
      color: rgba(var(--cl-f), 1);
    }

    @include mobile {
      padding: 20px 19px;
    }
  }

  &__button {
    width: clamp(300px, 22.667vw, 340px);
    height: 50px;
    font-family: var(--font-family);
  }
}
