.reviews-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__item {
    padding: clamp(28px, 2.4vw, 36px) 0 clamp(28px, 3.2vw, 48px);
    border-bottom: solid 1px var(--border-d);
    display: flex;
    flex-direction: column;
    width: 82%;
    grid-gap: 20px;

    &:first-child {
      padding: 0 0 clamp(28px, 3.2vw, 48px);
    }

    @include small-tablet {
      width: 100%;
    }
    @include mobile {
      grid-gap: 23px;
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
  }

  &__name {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: clamp(16px, 1.2vw, 18px);
    line-height: 120%;
    color: rgba(var(--cl-c), 1);
  }

  &__date {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 1.2vw, 18px);
    line-height: 120%;
    color: rgba(var(--cl-g), 1);
  }

  &__text {
    max-width: 1100px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: rgba(var(--cl-b), 1);
  }
}
