.news-info {
  margin: 28px 0 clamp(55px, 6.667vw, 100px);

  &__box {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;

    p {
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      color: rgba(var(--cl-c), 1);
      max-width: 92%;

      @include desktop {
        max-width: 100%;
      }
    }
  }

  &__title {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(18px, 2vw, 30px);
    line-height: 150%;
    color: rgba(var(--cl-c), 1);

    br {
      display: none;

      @include ios {
        display: inline;
      }
    }
  }
}
