.footer-top {
  width: 100%;
  position: relative;
  &__box {
    padding: clamp(30px, 2.733vw, 41px) 0;
    padding-right: 3%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: (1650px)) {
      padding-right: 0;
    }

    @include small-tablet {
      flex-direction: column;
      grid-gap: 30px;
    }
  }

  &__background {
    position: absolute;
    inset: 0;
    pointer-events: none;

    img,
    picture {
      width: 100%;
      height: 100%;
    }
  }

  .main-logo {
    @include tablet {
      display: none;
    }
  }
}
