.quantity-list {
  display: flex;
  align-items: center;
  grid-gap: clamp(10px, 1.4vw, 21px);
  padding: 9px 0;

  &__title {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: rgba(var(--cl-c), 1);
  }

  &__link {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: rgba(var(--cl-c), 1);
    position: relative;
    &::before {
      position: absolute;
      content: "";
      height: 37px;
      width: 37px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border: 1px solid transparent;
      border-radius: 50%;
      transition: all 0.4s;
    }

    &.active {
      &:before {
        border: 1px solid var(--border-c);
      }
    }
  }
}
