.novelties-slider {
  &__inner {
    width: 100%;
    max-height: clamp(300px, 46.875vw, 900px);
    position: relative;
  }

  &__slide {
    width: 100%;
    position: relative;
  }

  &__picture {
    width: 100%;
    height: 100%;
    position: absolute;
    inset: 0;

    img,
    picture {
      width: 100%;
      height: 100%;
    }
  }

  &__box {
    height: clamp(300px, 46.875vw, 900px);
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: relative;
  }

  &__nav {
    z-index: 5;
    height: fit-content;
    width: fit-content;
    flex-direction: column;
    position: absolute;
    left: calc(50% + (705px));
    top: 50%;
    transform: translateY(-40%);
    grid-gap: clamp(44px, 5.733vw, 86px);

    @media (max-width: (1550px)) {
      left: initial;
      right: 15px;
    }
  }

  &__next {
    width: clamp(28px, 2.933vw, 44px);
    height: clamp(15px, 1.6vw, 24px);
    transform: rotate(180deg);
    svg {
      stroke: var(--svg-fill-hover);
    }
  }

  &__prev {
    width: clamp(28px, 2.933vw, 44px);
    height: clamp(15px, 1.6vw, 24px);
    transform: rotate(0deg);
    svg {
      stroke: var(--svg-color-a);
    }
  }

  &__pagination {
    flex-direction: column;
    align-items: center;
    font-size: clamp(16px, 2.133vw, 32px);
    line-height: 120%;
    height: clamp(62px, 8vw, 120px);
    width: clamp(2px, 0.267vw, 4px);
    background-color: rgba(var(--bg-a), 0.8);
    color: rgba(var(--cl-a), 1);
    position: relative;

    &:before {
      content: attr(data-active);
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 500;
      font-size: clamp(16px, 2.133vw, 32px);
      line-height: 130%;
      color: rgba(var(--cl-a), 1);
      position: absolute;
      top: calc(clamp(10px, 1.333vw, 20px) * -1);
      transform: translateY(-100%);
    }
    &:after {
      position: absolute;
      bottom: calc(clamp(10px, 1.333vw, 20px) * -1);
      transform: translateY(100%);
      content: attr(data-slides);
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 500;
      font-size: clamp(16px, 2.133vw, 32px);
      line-height: 130%;
      color: rgba(var(--cl-a), 1);
    }
  }

  .swiper-pagination-bullet {
    display: flex;
    height: 20px;
    width: calc(100% + 2px);
    margin: 0 -1px;
    background-color: rgba(var(--bg-c), 1);
    opacity: 0;

    &-active {
      opacity: 1;
    }
  }

  .main-title {
    color: rgba(var(--cl-a), 1);
    max-width: clamp(181px, 44vw, 660px);
    padding-bottom: clamp(40px, 10.533vw, 158px);
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    font-size: clamp(24px, 3.067vw, 46px);
    text-transform: none;

    b {
      position: relative;
      left: 0;
      top: -30px;
      bottom: 0;
      width: clamp(37px, 6.667vw, 100px);
      height: clamp(4px, 0.667vw, 10px);
    }
  }
}
