.contact-form__label {
  width: 100%;
}
.contact-form__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: clamp(16px, 1.6vw, 24px);
  line-height: 150%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: rgba(var(--cl-c), 1);
}
.contact-form__input {
  height: 44px;
  text-indent: 16px;
  width: 100%;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(14px, 1.067vw, 16px);
  line-height: 130%;
  color: rgba(var(--cl-c), 1);
}
.contact-form__input::placeholder {
  color: rgba(var(--cl-f), 1);
}
.contact-form__button {
  width: clamp(300px, 21.333vw, 320px);
}
.contact-form__validation {
  width: 100%;
  justify-content: flex-start;
  display: flex;
  align-items: center;
  grid-gap: clamp(14px, 1.333vw, 20px);
}
.contact-form__validation-icon {
  height: clamp(40px, 6.133vw, 92px);
}
.contact-form__validation-icon img,
.contact-form__validation-icon picture {
  width: 100%;
  height: 100%;
}

.blog-tabs {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(30px, 2.6vw, 39px);
}
.blog-tabs__nav {
  display: flex;
  grid-gap: 10px;
  flex-wrap: wrap;
}
.blog-tabs__btn {
  padding: 12px 30px;
  background: rgba(var(--bg-l), 1);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(var(--cl-g), 1);
  position: relative;
  transition: all 0.4s;
}
.blog-tabs__btn.active::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  bottom: 0;
  background-color: rgba(var(--bg-c), 1);
  transition: all 0.4s;
}

.user-card {
  display: flex;
  align-items: flex-start;
  grid-gap: clamp(18px, 2vw, 30px);
  width: 100%;
  height: 100%;
}
.user-card--mode {
  justify-self: end;
  flex-direction: column;
  grid-gap: 20px;
  width: 205px;
}
.user-card--mode .user-card__box {
  grid-gap: 11px;
}
.user-card--mode .user-card__icon {
  width: clamp(80px, 8vw, 120px);
}
.user-card--mode .user-card__title {
  font-size: 20px;
  line-height: 120%;
}
.user-card--mode p {
  font-size: 18px !important;
}
.user-card__box {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(6px, 0.6vw, 9px);
}
.user-card__icon {
  width: clamp(80px, 11.8vw, 140px);
}
@media (max-width: 767px) {
  .user-card__icon {
    width: clamp(80px, 18.229vw, 140px);
    padding-bottom: 16px;
  }
}
.user-card__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(18px, 1.6vw, 24px);
  line-height: 150%;
  color: rgba(var(--cl-b), 1);
}
.user-card p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.333vw, 20px);
  line-height: 28px;
  color: rgba(var(--cl-n), 1);
}

.callback-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 20px;
  max-width: 600px;
  width: 100%;
}
.callback-form__label {
  width: 100%;
}
.callback-form__input {
  width: 100%;
  height: 44px;
  text-indent: 16px;
  border: 1px solid var(--border-b);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(14px, 1.067vw, 16px);
  line-height: 140%;
  color: rgba(var(--cl-c), 1);
}
.callback-form__input::placeholder {
  color: rgba(var(--cl-f), 1);
}
@media (max-width: 767px) {
  .callback-form__input {
    text-indent: 20px;
  }
}
.callback-form__button {
  height: 50px;
  width: clamp(300px, 22.667vw, 340px);
  margin-top: clamp(10px, 2vw, 30px);
}

.trade-accordion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
}
@media (max-width: 1024px) {
  .trade-accordion {
    grid-template-columns: 100%;
  }
}
.trade-accordion__item {
  display: flex;
  flex-direction: column;
  grid-gap: 13px;
  padding: clamp(20px, 2.667vw, 40px);
  background: rgba(var(--bg-l), 1);
}
@media (max-width: 767px) {
  .trade-accordion__item {
    grid-gap: 0;
  }
}
@media (max-width: 576px) {
  .trade-accordion__item {
    padding: 20px 15px 20px 19px;
  }
}
@media (min-width: 768px) {
  .trade-accordion__content {
    max-height: initial !important;
  }
}
.trade-accordion__content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 160%;
  color: rgba(var(--cl-c), 1);
}
@media (max-width: 767px) {
  .trade-accordion__content p:first-child {
    padding-top: 16px;
  }
}
@media (max-width: 576px) {
  .trade-accordion__content p br {
    display: none;
  }
}
@media (min-width: 768px) {
  .trade-accordion__content p {
    font-size: 16px;
  }
}
.trade-accordion__link {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: rgba(var(--cl-d), 1);
}
@media (min-width: 768px) {
  .trade-accordion__link {
    font-size: 16px;
  }
}
.trade-accordion__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 6px;
  position: relative;
  width: 100%;
  grid-gap: 5px;
}
@media (min-width: 768px) {
  .trade-accordion__header {
    pointer-events: none;
  }
}
.trade-accordion__icon {
  transition: all 0.4s;
}
@media (min-width: 768px) {
  .trade-accordion__icon {
    display: none;
  }
}
.trade-accordion__icon svg {
  stroke: var(--svg-color-e);
}
.trade-accordion__btn {
  text-align: start;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: clamp(16px, 1.333vw, 20px);
  line-height: 24px;
  color: rgba(var(--cl-c), 1);
  width: 100%;
  display: flex;
}
.trade-accordion__list {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(10px, 1vw, 15px);
  padding-top: 16px;
}
.trade-accordion__text {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 160%;
  color: rgba(var(--cl-c), 1);
  display: flex;
  align-items: flex-start;
  grid-gap: 10px;
}
.trade-accordion__text::before {
  content: "";
  width: 100%;
  max-width: 6px;
  height: 6px;
  margin-top: 11px;
  background-color: rgba(var(--bg-c), 1);
  display: block;
}
@media (max-width: 767px) {
  .trade-accordion__text {
    font-size: 16px;
  }
}
.trade-accordion__row {
  display: grid;
  grid-template-columns: 1.5fr 0.8fr 0.8fr;
  grid-gap: 10px;
}
@media (max-width: 1239px) {
  .trade-accordion__row {
    grid-template-columns: 1.1fr 0.8fr 1fr;
  }
}
@media (max-width: 1024px) {
  .trade-accordion__row {
    grid-template-columns: 1.2fr 0.9fr 1.15fr;
  }
}
@media (max-width: 767px) {
  .trade-accordion__row {
    grid-template-columns: 1fr;
  }
}
.trade-accordion__row.double {
  grid-template-columns: 1.2fr 1fr;
}
@media (max-width: 1440px) {
  .trade-accordion__row.double {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 1024px) {
  .trade-accordion__row.double {
    grid-template-columns: 1.1fr 1fr;
  }
}
@media (max-width: 767px) {
  .trade-accordion__row.double {
    grid-template-columns: 1fr;
  }
}

.trade-accordion__header:has(.trade-accordion__btn.active) .trade-accordion__icon {
  transform: scaleY(-1);
}

.entrance-top {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(50px, 7.2vw, 108px);
}
@media (max-width: 1239px) {
  .entrance-top {
    flex-direction: row;
  }
}
.entrance-top p {
  max-width: 426px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(18px, 1.6vw, 24px);
  line-height: 150%;
  color: rgba(var(--cl-c), 1);
}
.entrance-top p b {
  color: rgba(var(--cl-d), 1);
  font-size: clamp(18px, 1.733vw, 26px);
  line-height: 150%;
  font-weight: 500;
}
@media (max-width: 576px) {
  .entrance-top p {
    max-width: 185px;
  }
  .entrance-top p br {
    display: none;
  }
}
.entrance-top__icon {
  margin-left: -50px;
  width: clamp(143px, 26.333vw, 395px);
}
@media (max-width: 576px) {
  .entrance-top__icon {
    display: flex;
    align-items: flex-end;
    margin-left: -70px;
  }
}

